.App {
  text-align: center;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1000px;
  margin: auto;
  padding: 0 30px;
}

header nav {
  display: flex;
  gap: 20px;
}

a {
  text-decoration: none;
  color: inherit;
}

header,
footer {
  background: rgb(2, 0, 42);
  color: white;
}

main {
  min-height: 80vh;
}

.product img {
  width: 100%;
  height: 70%;
  object-fit: contain;
}

.products {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  max-width: 1000px;
  margin: 50px auto;
}
